.contenedor-principal {
  background: #1e2326;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99;
}

.contenedor__header {
  max-width: 110rem;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
}

.contenedor__logo a {
  font-family: "Righteous", sans-serif;
  font-size: 3.6rem;
  color: #1CB698;
  text-decoration: none;
}

.contenedor__nav ul {
  display: flex;
  list-style: none;
}

.contenedor__nav ul li a {
  text-align: center;
  color: #fff;
  margin: 0 1.5rem;
  padding: 0.3rem;
  transition: 0.5s;
  text-decoration: none;
}

.contenedor__nav ul li a:hover {
  color: #1CB698;
}

.nav--responsive {
  background-color: #1CB698;
  color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
  display: none;
}

.inicio {
  background: linear-gradient(to top, rgba(30, 35, 38, 0.8), rgb(30, 35, 38)), url("/build/img/fondo.jpg");
  background-size: cover;
  height: 100vh;
  color: #fff;
  display: flex;
  align-items: center;
}

.contenido-banner {
  padding: 2rem;
  background-color: #1e2326;
  max-width: 35rem;
  margin: auto;
  text-align: center;
  border-radius: 40px;
}

.contenedor-img img {
  margin-top: 4rem;
  border: 10px solid #1CB698;
  display: block;
  width: 80%;
  margin: auto;
  border-radius: 100%;
}

.contenido-banner h1 {
  margin-top: 4rem;
  font-size: 4.2rem;
  font-family: "Righteous", sans-serif;
}

.contenido-banner h2 {
  font-size: 1.5rem;
  font-weight: normal;
}

.contenido-banner .redes a {
  color: #fff;
  display: inline-block;
  text-decoration: none;
  border: 1px solid #fff;
  border-radius: 100%;
  width: 4.2rem;
  height: 4.2rem;
  line-height: 4.2rem;
  margin: 4rem 0.5rem;
  font-size: 2rem;
  transition: 0.3s;
}

.contenido-banner .redes a:hover {
  background-color: #1CB698;
  transform: scale(1.2);
}

.sobremi {
  background-color: #1e2326;
  color: #fff;
  padding: 5rem 2rem;
}
.sobremi__contenido {
  max-width: 110rem;
  margin: auto;
}
.sobremi__contenido h2 {
  font-size: 4.8rem;
  font-family: "Righteous", sans-serif;
  text-align: center;
  padding: 2rem 0;
}
.sobremi__contenido p {
  font-size: 1.8rem;
  line-height: 2.2rem;
  margin-bottom: 2rem;
}
.sobremi__contenido p span {
  color: #1CB698;
  font-weight: bold;
}
.sobremi .datos {
  display: flex;
}
.sobremi .datos .col {
  width: 50%;
}
.sobremi .datos .col h3 {
  font-size: 2.8rem;
  font-family: "Righteous", sans-serif;
  margin-bottom: 2.5rem;
}
.sobremi .datos .col ul {
  list-style: none;
}
.sobremi .datos .col ul li {
  margin: 1.2rem 0;
}
.sobremi .datos .col ul li strong {
  display: inline-block;
  color: #1CB698;
  width: 13rem;
}
.sobremi .datos .col ul li span {
  background-color: #1CB698;
  padding: 0.3rem;
  font-weight: bold;
  border-radius: 0.5rem;
}

.contenedor-intereses {
  display: flex;
  flex-wrap: wrap;
}
.contenedor-intereses .interes {
  width: 10rem;
  height: 10rem;
  background-color: #252A2E;
  border-radius: 1rem;
  margin: 0 1.5rem 1.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}
.contenedor-intereses .interes:hover {
  background-color: #1CB698;
}
.contenedor-intereses .interes i {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.sobremi button {
  cursor: pointer;
  background-color: transparent;
  border: 2px solid #fff;
  width: fit-content;
  display: block;
  margin: 2rem auto;
  padding: 1rem 2.2rem;
  font-size: 1.6rem;
  color: #fff;
  position: relative;
  z-index: 10;
}
.sobremi button .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #1CB698;
  z-index: -1;
  transition: 1s;
}

.sobremi button:hover .overlay {
  width: 100%;
}

.skills {
  background-color: #252A2E;
  color: #fff;
  padding: 5rem 2rem;
}
.skills__contenido {
  max-width: 110rem;
  margin: auto;
}
.skills__contenido h2 {
  font-size: 4.8rem;
  font-family: "Righteous", sans-serif;
  text-align: center;
  padding: 2rem 0;
}
.skills__contenido .datos {
  display: flex;
}
.skills__contenido .datos .col {
  width: 50%;
  padding: 0 2rem;
}
.skills__contenido .datos .col h3 {
  font-size: 2.8rem;
  font-family: "Righteous", sans-serif;
  margin-bottom: 2.5rem;
}

.skills .skill > span {
  font-weight: bold;
  display: block;
  margin-bottom: 1rem;
}

.skills .skill .barra-skill {
  height: 0.8rem;
  width: 80%;
  background-color: #131517;
  position: relative;
  margin-bottom: 3rem;
}
.skills .skill .barra-skill span {
  position: absolute;
  height: 4rem;
  width: 4rem;
  background-color: #1CB698;
  border-radius: 50%;
  line-height: 4rem;
  text-align: center;
  top: -17px;
  right: -15px;
  font-size: 14px;
}

.skills .skill .progreso {
  background-color: #1CB698;
  position: absolute;
  top: 0;
  left: 0;
  height: 0.8rem;
}

.skills .skill .javascript {
  width: 0%;
  animation: 3s javascript forwards;
}

@keyframes javascript {
  0% {
    width: 0%;
  }
  100% {
    width: 70%;
  }
}
.skills .skill .htmlCss {
  width: 0%;
  animation: 3s htmlCss forwards;
}

@keyframes htmlCss {
  0% {
    width: 0%;
  }
  100% {
    width: 80%;
  }
}
.skills .skill .gitHub {
  width: 0%;
  animation: 3s gitHub forwards;
}

@keyframes gitHub {
  0% {
    width: 0%;
  }
  100% {
    width: 85%;
  }
}
.skills .skill .react {
  width: 0%;
  animation: 3s react forwards;
}

@keyframes react {
  0% {
    width: 0%;
  }
  100% {
    width: 10%;
  }
}
.skills .skill .boots {
  width: 0%;
  animation: 3s boots forwards;
}

@keyframes boots {
  0% {
    width: 0%;
  }
  100% {
    width: 40%;
  }
}